import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ngfor',
  templateUrl: './ngfor.component.html',
  styleUrls: ['./ngfor.component.css'],
})
export class NgforComponent implements OnInit {
  constructor(private router: Router) {}

  ngOnInit(): void {}
  products = [
    {
      proimage:
        'https://images.unsplash.com/photo-1511707171634-5f897ff02aa9?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60',
      name: 'laptop',
      id: 'pro01',
      price: 35000,
    },
    {
      proimage:
        'https://images.unsplash.com/photo-1496181133206-80ce9b88a853?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60',
      name: 'Mobile',
      id: 'pro02',
      price: 15000,
    },
    {
      proimage:
        'https://images.unsplash.com/photo-1522152302542-71a8e5172aa1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60',
      name: 'TV',
      id: 'pro03',
      price: 15000,
    },
    {
      proimage:
        'https://images.unsplash.com/photo-1469504512102-900f29606341?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60',
      name: 'WAshing machine',
      id: 'pro04',
      price: 20000,
    },
  ];

  users = [];

  onCreateUser(uname) {
    this.users.push({
      name: uname.value,
    });

    if (this.users.length > 3) {
      alert('more than 3 users');
      this.router.navigate(['products']);
    }
  }

  onRemoveUser() {
    this.users.splice(this.users.length - 1);
  }

  onRemoveItem(item) {
    this.users.splice(item, 1);
  }
}
