import { NgModule } from '@angular/core';
// import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './about/about.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { ParentComponent } from './parent/parent.component';
import { ProductsComponent } from './products/products.component';
import { LaptopComponent } from './products/laptop/laptop.component';
import { TvComponent } from './products/tv/tv.component';
import { WashingMachineComponent } from './products/washing-machine/washing-machine.component';
import { MobileComponent } from './products/mobile/mobile.component';
import { ContactComponent } from './contact/contact.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { ManageProductsComponent } from './manage-products/manage-products.component';
import { TestproComponent } from './testpro/testpro.component';
import { CourseComponent } from './course/course.component';


const appRoutes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'about', component: AboutComponent },
  { path: 'home', component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'buy-products', component: ParentComponent },
  {
    path: 'products',
    component: ProductsComponent,
    children: [
      { path: 'laptop', component: LaptopComponent },
      { path: 'tv', component: TvComponent },
      { path: 'machine', component: WashingMachineComponent },
      { path: 'mobile', component: MobileComponent },
    ],
  },

  { path: 'contact', component: ContactComponent ,
  children: [
    { path: 'laptop', component: LaptopComponent },
    { path: 'tv', component: TvComponent },
    { path: 'machine', component: WashingMachineComponent },
    { path: 'mobile', component: MobileComponent },
  ],
  },
  
  { path: 'manage-products', component: ManageProductsComponent },
  { path: 'test', component: TestproComponent },
  { path: 'buy-course', component: CourseComponent },

  { path: '**', component: PageNotFoundComponent },
];

@NgModule({
  declarations: [ManageProductsComponent],
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
