import { Component, OnInit } from '@angular/core';
import { ProductdataService } from '../appServices/productdata.service';

@Component({
  selector: 'app-manage-products',
  templateUrl: './manage-products.component.html',
  styleUrls: ['./manage-products.component.css'],
})
export class ManageProductsComponent implements OnInit {
  constructor(private _productData: ProductdataService) {}
  ngOnInit(): void {}

  dataTitle = 'Your Products';

  products: any = [
    // {
    //   id: 'p11',
    //   name: 'laptop',
    //   price: 30000,
    // },
    // {
    //   id: 'p12',
    //   name: 'mobile',
    //   price: 15000,
    // },
    // {
    //   id: 'p13',
    //   name: 'tv',
    //   price: 40000,
    // },
  ];

  onAddProduct(ids, names, prices) {
    this.products.push({
      id: ids.value,
      name: names.value,
      price: prices.value,
    });
  }

  onSaveProduct() {
    this._productData.saveProducts(this.products).subscribe(
      (response) => console.log(response),
      (err) => console.log(err)
    );
  }

  onFetchProduct() {
    console.log('chale che');
  }
  onDeleteProduct(id) {
    if (confirm('Do you want to delete this product?')) {
      this.products.splice(id, 1);
    }
  }
}
